<template>
	<!-- 富文本编辑器 -->
	<div>

		<el-upload 
			class="avatar-uploader cus-avatar-uploader" 
			:action="serverUrl" 
			:headers="header"
			name="image"
			:show-file-list="false"
			:on-success="uploadSuccess" 
			:on-error="uploadError" 
			:before-upload="beforeUpload"
		 ></el-upload>

		<quill-editor 
			class="editor"
			v-model="content"
			ref="myQuillEditor" 
			:options="editorOption" 
			@blur="onEditorBlur($event)"
			@focus="onEditorFocus($event)"
			@change="onEditorChange($event)"
		 ></quill-editor>

	</div>
</template>

<script>
	const toolbarOptions = [ // 工具栏配置
		["bold", "italic", "underline", "strike"], // 加粗 斜体 下划线 删除线
		["blockquote", "code-block"], // 引用  代码块
		[{
			header: 1
		}, {
			header: 2
		}], // 1、2 级标题
		[{
			list: "ordered"
		}, {
			list: "bullet"
		}], // 有序、无序列表
		[{
			script: "sub"
		}, {
			script: "super"
		}], // 上标/下标
		[{
			indent: "-1"
		}, {
			indent: "+1"
		}], // 缩进
		// [{'direction': 'rtl'}],                         // 文本方向
		[{
			size: ["small", false, "large", "huge"]
		}], // 字体大小
		[{
			header: [1, 2, 3, 4, 5, 6, false]
		}], // 标题
		[{
			color: []
		}, {
			background: []
		}], // 字体颜色、字体背景颜色
		[{
			font: []
		}], // 字体种类
		[{
			align: []
		}], // 对齐方式
		["clean"], // 清除文本格式
		["link", "image", "video"] // 链接、图片、视频
	];

	import {
		quillEditor
	} from "vue-quill-editor";
	import "quill/dist/quill.core.css";
	import "quill/dist/quill.snow.css";
	import "quill/dist/quill.bubble.css";
	import store from '@/store'
	export default {
		props: {
			richContent: { //接收父组件传递过来的值
				type: String
			}
		},

		components: { //组件
			quillEditor
		},

		data() {
			return {
				content: this.richContent, //基于vue的单项数据流,子组件是不允许直接对父组件传来的值进行修改
				quillUpdateImg: false, // 根据图片上传状态来确定是否显示loading动画，刚开始是false,不显示
				editorOption: {
					placeholder: "",
					theme: "snow", // or 'bubble'
					placeholder: "请添加内容",
					modules: {
						toolbar: {
							container: toolbarOptions,
							handlers: {
								image: function(value) {
									if (value) {
										// 触发input框选择图片文件
										document.querySelector(".cus-avatar-uploader input").click();
										// console.log(document.querySelector(".cus-avatar-uploader input"))
									} else {
										// console.log(this.quill)
										this.quill.format("image", false);
									}
								},
							}
						}
					}
				},
				// el-upload配置
				serverUrl: "/api/upLoadImg", // 这里写你要上传的图片服务器地址
				// 有的图片服务器要求请求头需要有token
				header: {
					//Authorization: 'Bearer ' + store.getters.access_token  
				}
			};
		},

		methods: {
			onEditorBlur() {
				//失去焦点事件
			},
			onEditorFocus() {
				//获得焦点事件
			},
			onEditorChange() {
				//内容改变事件
				this.$emit("input", this.content); // 需在父组件中自定义方法@input  接收子组件向父组件传的值
			},

			// 富文本图片上传前
			beforeUpload() {
				// 显示loading动画
				this.quillUpdateImg = true;
			},

			// 图片上传成功方法,res为图片服务器返回的数据 注意数据返回结构
			uploadSuccess(res, file) { // res为图片服务器返回的数据 注意数据返回结构
				// 获取富文本组件实例
				let quill = this.$refs.myQuillEditor.quill;
				// 如果上传成功
				console.log(res)
				if (res.code == 0) {
					// 获取光标所在位置
					let length = quill.getSelection().index;
					// 插入图片  res.data为服务器返回的图片地址
					quill.insertEmbed(length, "image", res.data[0]);
					// 调整光标到最后
					quill.setSelection(length + 1);
				} else {
					this.$message.error("图片插入失败");
				}
				// loading动画消失
				this.quillUpdateImg = false;
			},
			// 富文本图片上传失败
			uploadError() {
				// console.log(this.quill)
				// loading动画消失
				this.quillUpdateImg = false;
				this.$message.error("图片上传失败");
			}
		}
	};
</script>

<style>
</style>
